import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'status'
})
export class OnlineStatusPipe implements PipeTransform {


	private param = {
		online(value){
			return (value) ? 'O' : 'O';
		},
		yesNo(value){
			return (value) ? '' : ''
		},
    interviewType(value){
      return (value) ? '' : '';
    }
	}


  transform(value: any, args?: any): any {

  	if(this.param[args]){
  		return this.param[args](value);
  	}
  	else{
  		return value;
  	}

  }

}



